@use '../helpers/responsive-font';

:root {
  font-size: 62.5%;
  body {
    margin: 0;
    // Body font size 16px
    font-size: 1.6rem;
  }
}

body {
  font-family: 'Roboto', sans-serif;
}

h1,
h2 {
  @include responsive-font.fluid-font(10vw, 38px, 150px, 35px);
  font-family: 'Anton', sans-serif;
  letter-spacing: 0.5rem;
}
